 .list {
     background-color: rgba(250, 255, 0, 0.15);
     font-size: larger;
     display: grid;
     align-items: center;
     justify-content: space-around;
 }
textarea {
    border-radius: 20px;
    padding: 10px 5px;
}


 .heading {
     display: grid;
     grid-template-columns: 1fr 1fr;
     justify-content: space-between;
     justify-items: center;
     margin: 5px 0;
     font-size: xx-large;
     font-weight: bold;
     align-items: center
 }
 .button {
     margin-top: 5px;
     margin-left: 5px;
     padding: 5px 10px;
     text-align: center;
     border: 0;
     border-radius: 5px;
     max-width: 150px;
     background: #EA9BD7;
     color: white;
     font-size: medium;
 }

 .addPostForm {
     display: grid;
 }
